<template>
  <div class="screen-container" v-loading="isLoading">
    <div class="screen-breadcrumb">
      <Breadcrumb>
        <router-link tag="li" :to="'/screen/'+$route.params.type">
          {{$route.params.type === 'top'?'':''}}
          {{$route.params.type === 'middle'?'':''}}
          {{$route.params.type === 'bottom'?'':''}}
        </router-link>
        <li></li>
      </Breadcrumb>
    </div>
    <div class="button-group">
      <Upload
        ref="upload"
        action="/noAction"
        :headers="{timeout:1000}"
        multiple
        :disabled="uploading"
        :file-list="fileList"
        :limit="15"
        :show-file-list="false"
        :format="Number(listData.content_type)===0?['jpg','jpeg','png','bmp']:['avi','mov','mp4']"
        :max-size="512000"
        :before-upload="beforeUpload"
        :on-format-error="uploadFormatError"
        :on-exceeded-size="uploadExceededSize"
        :on-exceed="uploadExceed">
        <button class="gym-btn plain-btn font-s upload-btn" :class="{'uploading-btn':uploading}"></button>
      </Upload>
      <button v-if="!isEmpty" class="delete-btn" @click="handleDelete">{{isDeleting?'':''}}</button>
      <button v-if="isDeleting" class="cancel-delete-btn" @click="isDeleting=false"></button>
      <div style="width: 200px;margin-left: 20px;overflow: hidden;margin-right: 20px">
        <Progress :percent="usedSpace>fileSize.max?100:usedSpace*100/fileSize.max"
                  :status="usedSpace>fileSize.max?'wrong':'normal'" hide-info></Progress>
      </div>
      <span>{{usedSpace.toFixed(2)}}M/{{fileSize.max}}M</span>
      <div class="button-group-right">
        <button class="gym-btn plain-btn font-s push-btn" :class="{'disable-push':!canPush}" @click="pushFolder">
          
        </button>
        <Poptip class="tip-box" trigger="hover" v-model="showTip" width="360" :transfer="false" placement="bottom">
          <span class="help-icon" ref="tipsBtn"></span>
          <div slot="content">
            <ul class="tips-container">
              <li></li>
              <li>{{folder_type==='bottom'?'1080x1920':'1920x1080'}}</li>
              <li>{{fileSize.max}}M</li>
              <template v-if="Number(listData.content_type)===0">
                <li>15</li>
              </template>
              <template v-else>
                <li>15</li>
              </template>
              <li></li>
              <li>15</li>
            </ul>
          </div>
        </Poptip>
      </div>
    </div>
    <div class="screen-empty" v-if="isEmpty">
      <img src="~@/assets/empty_logo.gif" alt="empty_logo">
      <p class="empty-title"></p>
    </div>
    <div class="list-container" v-else>
      <ul class="list-ul" :class="{'is-deleting':isDeleting}" ref="imgList">
        <li v-for="(item,index) in listData.files" :key="index"
            ref="operate" tabindex="0" @blur="operateOpening=false">
          <template v-if="Number(listData.content_type)===0">
            <img class="cover-img" :src="item.filePath" alt="" @click="handleCheck(item.fid)">
          </template>
          <template v-else>
            <div class="cover-img" @click="handleCheck(item.fid,item)">
              <video :src="item.filePath" ref="video" @canplay="getTime(item)"></video>
            </div>
            <div class="video-time" v-if="item.time">{{item.time.toFixed(2)}}s</div>
          </template>
          <p class="cover-img-name text-ellipsis">{{item.fileName}}</p>
          <div class="list-checkbox" v-if="isDeleting" @click="handleCheck(item.fid)"
               :class="{'list-checked':checked.indexOf(item.fid)!==-1}"></div>
          <div class="operate-box" v-if="!isDeleting">
            <Tooltip content="">
              <button @click="renameBtnClick(item)">
                <i class="iconfont gym-icon-edit"></i>
              </button>
            </Tooltip>
            <Tooltip content="">
              <button @click="handleDeleteSingle(String(item.fid))">
                <i class="iconfont gym-icon-trash"></i>
              </button>
            </Tooltip>
          </div>
        </li>
      </ul>
      <div class="screen-body-footer" v-if="false">
        <Page
          :total="Number(listData.total)"
          :current="Number(listData.pageNum)+1"
          :page-size="Number(listData.pageSize)"
          show-sizer show-elevator show-total :transfer="false"
          @on-change="pageNumChange"
          @on-page-size-change="pageSizeChange"
        />
      </div>
    </div>
    <div class="video-container" v-if="openingVideo" @click="openingVideo=false">
      <div class="video-box" @click.stop>
        <i class="close-btn" @click="openingVideo=false"></i>
        <video :src="openingVideo.filePath" controls autoplay></video>
      </div>
    </div>
    <ModalBody :visible="isRenaming" title="">
      <Form class="create-form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="110"
            @submit.native.prevent @keydown.native.enter.prevent>
        <FormItem label="" prop="name">
          <Input clearable style="width: 300px" size='default' placeholder="" ref="rename"
                 v-model="formValidate.name" @on-enter="handleRename"/>
        </FormItem>
        <FormItem label="" style="margin-top: 20px">
          <button class="gym-btn cancel-btn" @click='isRenaming=false'></button>
          <button class="gym-btn confirm-btn" @click="handleRename"></button>
        </FormItem>
      </Form>
    </ModalBody>
  </div>
</template>
<script>
import Breadcrumb from 'views/components/Breadcrumb'
import {
  getFolderAndFilesById,
  deleteFolderFiles,
  updateFileName,
  pushFolder,
  checkFile,
  pushTerminal,
  getSts,
  uploadImage
} from 'api/screen'
import { convertData } from 'utils/tool'
import { Progress } from 'iview'
import { Message, Upload } from 'element-ui'
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.min.css'
import ModalBody from 'views/components/ModalBody'
import throttle from 'lodash/throttle'
import OSS from 'ali-oss'

export default {
  components: {Breadcrumb, Upload, Progress, ModalBody},
  data () {
    const validateName = (rule, value, callback) => {
      if (String(value).trim().length) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      uploading: true,
      sts: {},
      showTip: false,
      isDeleting: false,
      isEmpty: false,
      isLoading: false,
      listData: {
        stores: [],
        machines: [],
        files: []
      },
      operateOpening: false,
      checked: [],
      openingVideo: false,
      isRenaming: false,
      formValidate: {
        name: ''
      },
      ruleValidate: {
        name: [
          {required: true, validator: validateName, trigger: 'change'}
        ]
      }
    }
  },
  computed: {
    fileSize: function () {
      if (Number(this.listData.content_type) === 0) {
        return {
          name: '3M',
          byte: 3 * 1024 * 1024,
          max: 30
        }
      } else {
        return {
          name: '500M',
          byte: 500 * 1024 * 1024,
          max: 500
        }
      }
    },
    folder_type: function () {
      switch (Number(this.listData.type)) {
        case 0:
          return 'top'
        case 1:
          return 'middle'
        case 2:
          return 'bottom'
      }
    },
    usedSpace: function () {
      if (Array.isArray(this.listData.files)) {
        let all = this.listData.files.reduce((total, file) => total + Number(file.fileSize), 0)
        return all / 1024
      } else {
        return 0
      }
    },
    fileList: function () {
      return this.listData.files.map((value) => {
        return {
          name: value.fileName,
          url: value.filePath
        }
      })
    },
    folderName: function () {
      return this.listData.folderName.length > 13 ? this.listData.folderName.substr(0, 13) + '......' : this.listData.folderName
    },
    canPush: function () {
      if (Number(this.listData.pushIsAll)) {
        return true
      }
    }
  },
  watch: {
    '$route': function (value, oldValue) {
      this.getList(Object.assign({}, this.$route.query))
    },
    isDeleting: function (value) {
      if (value) {
        this.viewer.destroy()
      } else {
        this.viewer = new Viewer(this.$refs.imgList)
      }
    },
    'listData.pushIsAll': function (value) {
      if (Number(value)) {
        let tips = ''
        window.onbeforeunload = function (e) {
          e = e || window.event
          if (e) {
            e.returnValue = tips
          }
          return tips
        }
      } else {
        window.onbeforeunload = null
      }
    },
    'uploading': function (value) {
      if (value === 0) {
        this.updateProgress(99)
        console.log('', this.uploadedFiles)
        this.uploadedFiles.forEach(file => {
          delete file['index']
          delete file['percent']
        })
        uploadImage(this.uploadedFiles).then(res => {
          if (Number(res.code) === 200) {
            Message.closeAll()
            this.$message({
              message: '2',
              type: 'success'
            })
            this.getList(Object.assign({}, this.$route.query))
          } else {
            this.$message({
              message: res.message,
              type: 'warning'
            })
            this.getList(Object.assign({}, this.$route.query))
          }
        })
      }
    }
  },
  mounted () {
    this.getList(Object.assign({}, this.$route.query))
    getSts({}).then(res => {
      if (res.AccessKeyId && res.AccessKeySecret) {
        this.uploading = false
        this.sts = res
        this.validityTime = setTimeout(() => {
          window.location.reload()
        }, 15 * 60 * 1000)
      } else {
        this.uploading = true
        this.$message({
          message: '',
          type: 'warning'
        })
      }
    }).catch(() => {
      this.uploading = true
      this.$message({
        message: '',
        type: 'warning'
      })
    })
  },
  destroyed () {
    window.onbeforeunload = null
    clearInterval(this.progressTime)
    clearTimeout(this.validityTime)
    Message.closeAll()
  },
  beforeRouteLeave (to, from, next) {
    if (to.path === '/login') {
      next()
    }
    if (this.canPush) {
      this.$confirm({
        title: '',
        brief: '',
        confirm: (dialog) => {
          dialog.loading = true
          pushFolder({folderId: this.$route.params.id}).then(res => {
            if (Number(res.code) === 200) {
              this.$message({
                message: '',
                type: 'success'
              })
              setTimeout(() => {
                dialog.destroy()
                next()
              }, 500)
            } else {
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        },
        cancel: (dialog) => {
          dialog.destroy()
          next()
        }
      })
      next(false)
    } else {
      next()
    }
  },
  methods: {
    pushFolder () {
      if (!this.canPush) {
        return
      }
      pushFolder({folderId: this.$route.params.id}).then(res => {
        if (Number(res.code) === 200) {
          this.getList(Object.assign({}, this.$route.query))
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      })
    },
    renameBtnClick (item) {
      this.formValidate.name = item.fileName
      this.isRenaming = item.fid
      this.$nextTick(function () {
        this.$refs.rename.focus()
      })
    },
    handleRename () {
      if (!String(this.formValidate.name).trim()) {
        return
      }
      updateFileName({
        fileName: this.formValidate.name,
        fid: this.isRenaming,
        folderId: this.$route.params.id
      }).then(res => {
        if (Number(res.code) === 200) {
          this.isRenaming = false
          this.$message({
            message: '',
            type: 'success'
          })
          this.getList(Object.assign({}, this.$route.query))
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      })
    },
    getTime (item) {
      this.$refs.video.forEach((ele, index) => {
        if (this.listData.files[index] === item) {
          this.$set(this.listData.files, index, Object.assign({time: ele.duration}, item))
        }
      })
    },
    deleteAll () {
      this.isDeleting = true
      if (this.checked.length === this.listData.files.length) {
        this.checked = []
      } else {
        this.checked = this.listData.files.map(v => v.fid)
      }
    },
    handleDelete () {
      if (this.isDeleting) {
        if (this.checked.length === 0) {
          this.$message({
            message: '',
            type: 'warning'
          })
        } else {
          this.$confirm({
            title: '',
            brief: '',
            confirm: (dialog) => {
              dialog.loading = true
              this.deleteFiles(this.checked.join(','), dialog)
            }
          })
        }
      } else {
        this.checked = this.listData.files.map(v => v.fid)
        this.isDeleting = true
      }
    },
    handleDeleteSingle (id) {
      this.$confirm({
        title: '',
        brief: '',
        confirm: (dialog) => {
          dialog.loading = true
          this.deleteFiles(id, dialog)
        }
      })
    },
    deleteFiles (ids, dialog) {
      deleteFolderFiles({
        folderId: this.$route.params.id,
        fid: ids
      }).then((res) => {
        if (Number(res.code) === 200) {
          this.checked = []
          this.isDeleting = false
          dialog.destroy()
          this.$message({
            message: '',
            type: 'success'
          })
          this.getList(Object.assign({}, this.$route.query))
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      })
    },
    handleCheck (id, item) {
      if (this.isDeleting) {
        let index = this.checked.indexOf(id)
        if (index === -1) {
          this.checked.push(id)
        } else {
          this.checked.splice(index, 1)
        }
      } else if (Number(this.listData.content_type) !== 0) {
        this.openingVideo = item
      }
    },
    beforeUpload (file) {
      if (Number(this.listData.content_type) === 0) {
        if (['image/jpg', 'image/jpeg', 'image/png', 'image/bmp'].indexOf(file.type) === -1) {
          this.uploadFormatError()
          return false
        }
      } else {
        if (['video/avi', 'video/mov', 'video/mp4'].indexOf(file.type) === -1) {
          this.uploadFormatError()
          return false
        }
      }
      if ((this.usedSpace + file.size / 1024 / 1024) > this.fileSize.max) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return false
      }
      if (file.size > this.fileSize.byte) {
        this.uploadExceededSize()
        return false
      }
      if (!this.uploading) {
        this.preFiles = {}
        this.uploadedFiles = []
        this.uploading = 1
        this.uploadLength = 1
        this.$message({
          customClass: 'yellow-message',
          iconClass: 'no',
          message: '',
          duration: 0
        })
      } else {
        this.uploading++
        this.uploadLength++
      }
      let suffix = file.name.split('.')
      suffix = suffix[suffix.length - 1]
      let uploadFile = {
        index: this.uploading,
        percent: 0,
        folderId: this.$route.params.id,
        fileName: file.name.trim(),
        originalName: new Date().getTime() + '-' + Math.floor(Math.pow(10, 10) * Math.random()) + '.' + suffix,
        fileSize: file.size / 1024
      }
      this.preFiles[this.uploading] = uploadFile
      let inTime = new Date().getTime()
      new OSS({
        accessKeyId: this.sts.AccessKeyId,
        accessKeySecret: this.sts.AccessKeySecret,
        stsToken: this.sts.SecurityToken,
        region: 'oss-cn-shenzhen',
        bucket: 'advertisa'
      }).multipartUpload(uploadFile.originalName, file, {
        progress: this.handleProgress.bind(this, uploadFile)
      }).then((result) => {
        uploadFile['filePath'] = result.res.requestUrls[0].split('?')[0]
        this.uploadedFiles.push(uploadFile)
      }).finally(() => {
        // 
        let outTime = new Date().getTime()
        if ((outTime - inTime) < 1000) {
          setTimeout(() => {
            this.uploading--
          }, 1000 - outTime + inTime)
        } else {
          this.uploading--
        }
      })
      return false
    },
    handleProgress (file, progress) {
      if (this.uploading !== 0) {
        this.preFiles[file.index]['percent'] = progress * 100
        this.countProgress()
      }
    },
    countProgress: throttle(function () {
      if (this.uploading !== 0) {
        let total = this.uploadLength * 100
        let upload = Object.keys(this.preFiles).reduce((total, key) => total + this.preFiles[key]['percent'], 0)
        this.updateProgress(upload / total * 100)
      }
    }, 1000, {leading: false}),
    updateProgress (percent) {
      this.$message({
        customClass: 'yellow-message',
        iconClass: 'no',
        message: '' + Number(percent).toFixed(2) + '%',
        duration: 0
      })
    },
    uploadFormatError () {
      Message.closeAll()
      if (Number(this.listData.content_type) === 0) {
        this.$message({
          message: 'jpg,jpeg,png,bmp',
          type: 'warning'
        })
      } else {
        this.$message({
          message: 'avi,mov,mp4',
          type: 'warning'
        })
      }
    },
    uploadExceededSize () {
      Message.closeAll()
      this.$message({
        message: '' + this.fileSize.name + '',
        type: 'warning'
      })
    },
    uploadExceed () {
      this.$message({
        message: '15',
        type: 'warning'
      })
    },
    easyQuery (data = {}) {
      let prevQuery = this.$route.query
      let query = Object.assign({}, prevQuery, data)
      this.$router.push({path: this.$route.path, query})
    },
    getList (data = {}) {
      this.checked = []
      this.isDeleting = false
      this.isLoading = true
      data['id'] = this.$route.params.id
      data['pageNum'] = data['pageNum'] || 0
      data['pageSize'] = data['pageSize'] || 20
      getFolderAndFilesById(data).then((res) => {
        if (Number(res.code) === 200) {
          convertData(res.data, {
            'contentType': 'content_type',
            'screenType': 'type',
            'machineIds': 'machine_ids',
            'unlimitedTime': 'unlimited_time'
          })
          this.listData = res.data
          this.listData['machines'] = []
          this.listData['stores'] = []
          this.listData['total'] = res.total || 0
          this.listData['pageNum'] = res.pageNum
          this.listData['pageSize'] = res.pageSize
          this.listData['machine_name'] = this.listData['machine_name'] || ''
          this.isLoading = false
          if (Array.isArray(this.listData.files) && this.listData.files.length) {
            this.isEmpty = false
            this.$nextTick(function () {
              if (this.viewer) {
                this.viewer.destroy()
              }
              this.viewer = new Viewer(this.$refs.imgList)
            })
            if (this.notFirst && Number(this.listData.pushIsAll)) {
              this.showTip = true
              setTimeout(() => {
                this.showTip = false
              }, 10000)
            } else {
              // this.checkFile({
              //   folder_id: res.data.id,
              //   type: res.data.type
              // })
            }
          } else {
            this.isEmpty = true
          }
        } else {
          this.listData = {
            stores: [],
            machines: [],
            files: []
          }
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
        if (!this.notFirst) {
          this.notFirst = true
          this.showTip = true
          setTimeout(() => {
            this.showTip = false
          }, 10000)
        }
        this.isLoading = false
      })
    },
    checkFile (data) {
      checkFile(data).then(res => {
        if (Number(res.code) === 200) {
          if (res.data && res.data.length !== 0) {
            this.$alert({
              title: '',
              brief: '',
              confirm: () => {
                this.handleWarning(res.data)
              }
            })
          }
        }
      })
    },
    handleWarning (deleted) {
      let storeIdArray = this.listData.stores_id.split(',')
      let storeNameArray = this.listData.stores_name.split(',')
      let data = {
        folder_id: this.listData.id,
        stores_id: deleted.map(v => {
          let storeName = v.machieName.split('-')[0]
          let index = storeNameArray.indexOf(storeName)
          return storeIdArray[index]
        }).join(','),
        machineId: deleted.map(v => v.device_id).join(',')
      }
      pushTerminal(data).then(res => {
        if (Number(res.code) === 200) {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      })
    },
    pageNumChange (pageNum) {
      this.easyQuery({
        pageNum: String(pageNum - 1)
      })
    },
    pageSizeChange (pageSize) {
      this.easyQuery({
        pageNum: String(0),
        pageSize: pageSize
      })
    }
  }
}
</script>
