        border 2px solid white
        border-top none
        border-right none
        transform rotate(-48deg)
  .screen-empty
    flex 1
    display flex
    justify-content center
    align-items center
    flex-direction column
  .empty-title
    font-size 12px
    color #b1b1b1
    margin-top 10px
    > a
      color #24CF5F
  .list-container
    padding 0 30px
    flex 1
    overflow hidden
    display flex
    flex-direction column
  .list-ul
    padding-top 2px
    flex 1
    display flex
    flex-wrap wrap
    margin-right -66px
    overflow auto
    align-content flex-start
    & > li
      outline none
      width 144px
      height 180px
      border-radius 4px
      margin-right 66px
      display flex
      flex-direction column
      align-items center
      padding 12px
      margin-bottom 30px
      position relative
      &:hover .operate-box
        opacity 1

    &.is-deleting
      .cover-img
        cursor pointer
    &.is-deleting > li:hover
      box-shadow 0 0 6px 0 rgba(56, 105, 215, 0.2)
  .operate-btn
    outline none
    position absolute
    top 12px
    right 12px
    width 0
    height 0
    border-top 20px solid rgba(0, 0, 0, .3)
    border-right 20px solid rgba(0, 0, 0, .3)
    border-left 20px solid transparent
    border-bottom 20px solid transparent
    cursor pointer
    .operate-dot-group
      position absolute
      top -8px
      right -16px
      overflow hidden
      display flex
    .operate-dot
      width 3px
      height 3px
      border-radius 50%
      background white
      display inline-block
      margin-left 3px
  .operate-list
    position absolute
    top 72px
    right -20px
    width 80px
    height 80px
    font-size 14px
    background white
    box-shadow 0 0 6px 0 rgba(102, 102, 102, 0.2)
    border-radius 4px
    border 1px solid #EAEAEA
    display flex
    text-align center
    flex-direction column
    padding 16px 0
    align-items stretch
    justify-content space-between
    & > li:hover
      color #2162D2
      cursor pointer
  .cover-img
    width 120px
    height 120px
    background #E0E2E3
    border none
  .cover-img-name
    margin-top 15px
    color #666666
    font-size 14px
    max-width 100%
  .screen-body-footer
    padding-right 20px
    height 60px
    width 100%
    display flex
    justify-content flex-end
    align-items center
  .list-checkbox
    cursor pointer
    position absolute
    top 22px
    left 22px
    width 12px
    height 12px
    background white
    border-radius 4px
    border 1px solid #D6D6D6
    &.list-checked
      background #2162D2
      border 1px solid #2162D2
      &:before
        content ''
        position absolute
        top 1px
        left 1px
        width 8px
        height 5px
        border-left 1px solid white
        border-bottom 1px solid white
        transform rotate(-45deg)
  .cover-img video
    width 100%
    height 100%
    cursor pointer
  .video-container
    position fixed
    top 0
    left 0
    z-index 2
    width 100%
    height 100%
    background rgba(0, 0, 0, 0.3)
    display flex
    align-items center
    justify-content center
  .video-box
    width 800px
    height 450px
    background black
    position: relative
    & > video
      width 100%
      height 100%
  .close-btn
    position: absolute
    top -40px
    right -40px
    height 30px
    width 30px
    background url('~@/assets/white_wrong.png') no-repeat center
    background-size 100% 100%
    cursor pointer
  .video-time
    position absolute
    top 112px
    left -9px
    right 0
    width 120px
    margin 0 auto
    height 20px
    color #999
    text-align right
  .tip-box /deep/ .ivu-poptip-inner
    background rgba(0, 0, 0, 0.6);
    box-shadow 0 0 6px 0 rgba(102, 102, 102, 0.2)
  .tip-box /deep/ .ivu-poptip-popper[x-placement^=bottom] .ivu-poptip-arrow:after
    border-bottom-color rgba(0, 0, 0, 0.6)
    top 0
  .tips-container
    color white
    line-height 24px
    & > li:before
      content ''
      display inline-block
      width 6px
      height 6px
      background white
      border-radius 50%
      margin-right 8px
      position relative
      top -1px
  .selected-time-container
    display: inline-block
    position: relative
    margin-right 14px
  .selected-time-tips
    position absolute
    top 0
    right -72px
    bottom 0
    margin auto 0
    cursor pointer
    color #2162d2
  .selected-time-picker
    width 60px
  .selected-time-picker /deep/ .ivu-date-picker-rel
    opacity 0
    cursor pointer
  .selected-time-picker /deep/ .ivu-picker-confirm
    display none
  .selected-time-picker /deep/ .ivu-picker-panel-content
    position relative
  .selected-time-picker /deep/ .ivu-picker-panel-content:before
    content ''
    position absolute
    top 0
    left 0
    z-index 10
    width 100%
    height 100%
    background white
    opacity 0
  .selected-time-picker /deep/ .ivu-date-picker-cells-cell-selected em
    background #b1b1b1
  .selected-time-picker /deep/ .ivu-date-picker-cells-cell-selected:hover em
    background #b1b1b1
  .selected-time-picker /deep/ .ivu-date-picker-cells-focused em
    box-shadow none
  .selected-time-picker /deep/ .ivu-date-picker-cells-cell-today em:after
    display none
  .screen-breadcrumb
    height 64px
    display flex
    align-items center
    border-bottom 1px solid #EAEAEA
  .operate-box
    opacity 0
    transition opacity 0.2s
    position absolute
    top 18px
    left 0
    padding 0 18px
    width 100%
    display flex
    flex-direction row
    justify-content space-between
    button
      background rgba(237, 237, 237, .4)
      color #524D4D
      width 20px
      height 20px
      border-radius 4px
    .iconfont
      font-size 18px
      position relative
      top -4px
  .cancel-btn
    border 1px solid #2162d2
    color #2162d2
    margin-right 20px
  .confirm-btn
    background #2162d2
    color white
</style>



